.mainapp {
    margin: 20px;
    display: flex;
    flex-direction: column;
}

.questionblock {
    background-color: white;
    border: 1px solid rgb(0, 158, 158);
    border-radius: 10px;
    width: 100%;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.output {
    width: 90%;
    margin-top: 15px;
}

.checkblock {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.halfblock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 40%;
}

.halfblock{
    color: teal;
}

.fullshort {
    border: 2px solid rgb(255, 255, 255);
    height: 30px;
    border-radius: 15px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;

    background-color: #00ccff;
}

.fullshort h4 {
    width: 50%;
    color: white;
    cursor: pointer;
}

.fullshort h4:hover {
    color: rgb(109, 255, 255);
}

.lines {
    border-left: 2px solid rgb(255, 255, 255);
}

.answerblock {
    width: 100%;
    padding: 20px;
    margin-top: 20px;
    border-radius: 15px;
    color: white;
}