.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 20px;
  min-height: 360px;
}

.imglogo {
  width: 70%;
  margin-top: 50px;
  margin-bottom: 30px;
}

.linkblock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 150px;
  margin: 30px auto;
  width: 40%;
  /* border: 1px solid black; */
  color: white;
}

.linkblock h2:hover {
  color: #00ffff;
}

.link {
  color: white;
  text-decoration: none;
}

.max {
  height: 100%;
}